import React from 'react';
import styled from 'styled-components';

import FadeIn from "components-common/FadeIn";
import { ARTWORK_WIDTH } from "constants/view";
import { imageScalerByWidth } from "lib/layout-utils";

const ArtworkItem = styled.div`
  margin-top: 15px;
`
const ArtworkImg = styled.img`
  width: 100%;
`

const ArtworkList = ({ artworkImgList }) => {
  const artworkList =  artworkImgList && artworkImgList.map(artworkImg => {
    const { 
      fields: { file: imgFile } = {}, 
      sys: { id: imgId } = {},
    } = artworkImg;
    const { 
      url: imgUrl,
      details: imgDetails,
    } = imgFile;
    const { 
      image: { 
        height: imgHeight, 
        width: imgWidth,
      } 
    } = imgDetails;

    const coverImgScaledHeight = imageScalerByWidth(imgWidth, imgHeight, ARTWORK_WIDTH);

    return (
      <ArtworkItem key={imgId}>
        <FadeIn height={coverImgScaledHeight}>
          {onLoad => 
            <ArtworkImg src={imgUrl} alt='artwork' onLoad={onLoad}/>
          }
        </FadeIn>
      </ArtworkItem>
    );
  });

  return artworkList;
}

export default ArtworkList;